import styled from "styled-components";

export const ProductsContainer = styled.div`
  width: 100%;
  min-height: 100vh;
  padding: 5rem calc((100vw-1300px) / 2);
  background: #150f0f;
  color: #fff;
`;

export const ProductWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
`;

export const ProductCard = styled.div`
  margin: 0 2rem;
  line-height: 2;
  width: 300px;
`;

export const ProductImg = styled.img`
  height: 300px;
  min-width: 300px;
  max-width: 100%;
  box-shadow: 6px 6px #fdc500;
`;

export const ProductsHeadings = styled.h1`
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
  padding-top: 2rem;
`;

export const ProductTitle = styled.h2`
  font-size: 2rem;
  font-weight: 500;
`;

export const ProductInfo = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  text-align: center;
`;

export const ProductDesc = styled.p`
  margin-bottom: 1rem;
`;

export const ProductPrice = styled.p`
  margin-bottom: 1rem;
  font-size: 2rem;
`;

export const ProductButton = styled.button`
  font-size: 1rem;
  padding: 1rem 3rem;
  border: none;
  background: #e31837;
  color: #fff;
  transition: 0.2s ease-out;

  &:hover {
    background: #ffc500;
    transition: 0ms.2s ease-out;
    cursor: pointer;
    color: #000;
  }
`;

export const ProductLine = styled.div`
  height: 0.25rem;
  width: 10rem;
  color: #fff;
  font-size: 2rem;
  background: #fff;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 2rem 4rem;
`;
