import styled from "styled-components";
import FeaturedPic from "../../images/featured3.jpeg";

export const FeatureContainer = styled.div`
  height: 100vh;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.1)),
    url(${FeaturedPic});
  max-height: 500px;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  padding: 1rem 1rem;

  h1 {
    font-size: 3rem;
  }

  p {
    margin-bottom: 1rem;
    font-size: 2rem;
  }
`;

export const FeatureButton = styled.button`
  font-size: 1.4rem;
  padding: 1rem 3rem;
  background: #ffc500;
  border: 1px solid;
  transition: 0.2s ease-out;
  color: #fff;
  border-radius: 20px;

  &:hover {
    transition: 0.2s ease-out;
    color: #ffc500;
    background: #e31837;
    cursor: pointer;
  }
`;
