import React from "react";
import {
  ProductButton,
  ProductCard,
  ProductDesc,
  ProductPrice,
  ProductInfo,
  ProductImg,
  ProductTitle,
  ProductsContainer,
  ProductsHeadings,
  ProductWrapper,
  ProductLine,
} from "./ProductsElements";

const Products = ({ heading, data }) => {
  return (
    <ProductsContainer>
      <ProductsHeadings>{heading}</ProductsHeadings>
      <ProductWrapper>
        {data.map((products, index) => {
          return (
            <ProductCard key={index}>
              <ProductImg src={products.img} alt={products.alt} />
              <ProductInfo>
                <ProductTitle>{products.name}</ProductTitle>
                <ProductDesc>{products.desc}</ProductDesc>
                <ProductPrice>{products.price}</ProductPrice>
                <ProductButton>{products.button}</ProductButton>
              </ProductInfo>
              <ProductLine></ProductLine>
            </ProductCard>
          );
        })}
      </ProductWrapper>
    </ProductsContainer>
  );
};

export default Products;
