import React from "react";
import {
  FaFacebookF,
  FaInstagram,
  FaLocationArrow,
  FaYoutube,
} from "react-icons/fa";
import {
  SocialIcons,
  SocialMediaWrap,
  SocialLogo,
  SocialLink,
  FooterContainer,
  FooterWrap,
  SocialMedia,
} from "./FooterElements";

const Footer = () => {
  return (
    <FooterContainer>
      <FooterWrap>
        <SocialMedia>
          <SocialMediaWrap>
            <SocialLogo to="/">Pizza</SocialLogo>
            <SocialIcons>
              <SocialLink
                href="/"
                target="_blank"
                aria-label="Facebook"
                rel="noopener noreferer"
              >
                <FaFacebookF />
              </SocialLink>
              <SocialLink
                href="/"
                target="_blank"
                aria-label="Instagram"
                rel="noopener noreferer"
              >
                <FaInstagram />
              </SocialLink>
              <SocialLink
                href="/"
                target="_blank"
                aria-label="YouTube"
                rel="noopener noreferer"
              >
                <FaYoutube />
              </SocialLink>
              <SocialLink
                href="/"
                target="_blank"
                aria-label="Location"
                rel="noopener noreferer"
              >
                <FaLocationArrow />
              </SocialLink>
            </SocialIcons>
          </SocialMediaWrap>
        </SocialMedia>
      </FooterWrap>
    </FooterContainer>
  );
};

export default Footer;
